export default {
    name: 'app-input',
    props: {
        type: String,
        password: Boolean,
        disabled: Boolean,
        placeholder: {
            default: '',
            type: String,
        },
        autoHeight: Boolean,
        showConfirmBar: Boolean,
        placeholderStyle: {
            type: String,
            default: function () {
                return 'color: #999999';
            }
        },
        maxLength: String,
        value: {
            default: '',
        },
        round: Boolean,
        border: {
            type: [Number, Boolean]
        },
        borderColor: {
            default: '#c0c4cc',
        },
        icon: String,
        center: Boolean,
        size: String,
        width: String,
        color: {
            default: '#353535',
            type: String,
        },
        height: String,
        backgroundColor: String,
        radius: Number,
        focus: {
            type: Boolean,
            default: false,
        },
        paddingLeft: {
            default: 12,
        },
        defaultValue: {
            default: '',
        },
    },
    data() {
        return {
            newValue: this.value
        }
    },
    methods: {
        blur(event) {
            this.$emit('blur', event.detail);
        },
        confirm(event) {
            this.$emit('confirm', event.detail);
        },
        changeValue(value) {
            this.$emit('input', value.detail.value);
        }
    },
    computed: {
        setHeight: function () {
            if (this.height) return this.height;
            switch (this.size) {
                case 'large':
                    return 100;
                case 'small':
                    return 60;
                case 'medium':
                    return 80;
                default:
                    return 80;
            }
        }
    }
}
